import axios from 'axios';
import React, { useState } from 'react'
import { Box, Tag, Text, Flex, Input,Button,  Heading } from '@chakra-ui/react';
import { UilLocationPoint } from '@iconscout/react-unicons'


function Weather(){
   const [country, setCountry] = useState('');
   const [data, setData] = useState({})

   const api = {
    Key:"77b6b4742953309d9e98ab25cb8c86a4",
    base:`https://api.openweathermap.org/data/2.5/weather?q=${country}&appid=4f96e55ac2eb8c6d5a2ee28db0705405`,
    default: "https://api.openweathermap.org/data/2.5/onecall"
   }
    var time = new Date();
    var days = ['Sun', 'Mon', 'Tues','Wed','Thur','Fri','Sat'];
    var day=days[time.getDay()];
    var months=['January','February','March','April','May','June','July','August','September','October','November','December']
    var month= months[time.getMonth()];
    var date = time.getDate();
    var year = time.getFullYear();
    var hour = time.getHours();
    var minutes = time.getMinutes();
    if(hour < 10){
      hour = '0' + hour
    }
   if(minutes < 10){
    minutes = '0' + minutes
   }
  const checkCountry = async () => {
    try {
      const geoResponse = await axios.get(
         `https://api.openweathermap.org/geo/1.0/direct?q=${country}&limit=1&appid=4f96e55ac2eb8c6d5a2ee28db0705405`
      )
      const geoData = geoResponse.data

      const OneCallResponse = await axios.get(
        `https://api.openweathermap.org/data/2.5/onecall?lat=${geoData[0].lat}&lon=${geoData[0].lon}&exclude=minutely,daily,alerts&appid=4f96e55ac2eb8c6d5a2ee28db0705405`
      )
      const OneCallData = OneCallResponse.data

      setData({ ...OneCallData, Name:geoData[0].name, capital:geoData[0].country})
    } catch (error) {
      console.log(error)
    }
  } 

  return (
    <Box>
       <Box  pb={2} d='flex'>
      <Input type={'text'} placeholder='enter location' bg={'white'}
      value={country}  id='search'  w={'50'}
          onChange={event => setCountry(event.target.value)} />
        <Button variant={'ghost'} onClick={checkCountry}>
          <i className='fa fa-search' aria-hidden='true'></i>
         </Button>
      </Box>
          <Box minHeight={'60vh'} minWidth={'50vw'}
      backgroundBlendMode="overlay" borderRadius={6} boxShadow='0px 8px 24px rgba(0,0,0,0.9)' p={8}  mx={'auto'}>
      <Box>
        <Text fontSize={80} fontWeight={5}>
                <UilLocationPoint size='50' color='red' />{data.Name ? data.Name : 'Nigeria'}, {data.capital ? data.capital : 'NG'} </Text>
      </Box>
      <Tag size={'lg'} variant={'solid'} bg={'teal'} p={4} m={10} borderRadius={5} >
        {day} {date} {month} {year} <br />  {hour}:{minutes}
            </Tag>
            <Box>
              <Text fontSize={40} fontWeight={20}>{data.current ? data.current.temp : ('80')}F</Text> <br />
          <Text fontSize={30} fontWeight={20}>{data.current ? data.current.weather[0].description : ('Sunny')}</Text>
            </Box>
      </Box>
      <Flex >
        <Box minHeight={'30vh'} minWidth={'23vw'}
          backgroundBlendMode="overlay" borderRadius={6} boxShadow='0px 8px 24px rgba(0,0,0,0.9)' mt={15} p={8} mx={'auto'}>
          <Heading as={'h3'} >Last 3 hours</Heading>
          <Text fontSize={30} fontWeight={10}>{data.current ? data.hourly[0].temp : ('80')}F</Text> <br />
          <Text fontSize={20} fontWeight={10}>{data.current ? data.hourly[0].weather[0].description : ('Sunny')}</Text>
          </Box>
         <Box minHeight={'30vh'} minWidth={'23vw'}
          backgroundBlendMode="overlay" borderRadius={6} boxShadow='0px 8px 24px rgba(0,0,0,0.9)' p={8} mt={15} mx={'auto'}>
          <Heading as={'h2'} >Next 3 hours</Heading>
          <Text fontSize={30} fontWeight={10}>{data.current ? data.hourly[3].temp : ('80')}F</Text> <br />
          <Text fontSize={20} fontWeight={10}>{data.current ? data.hourly[3].weather[0].description : ('Sunny')}</Text>
      </Box>
      </Flex>
   </Box>
  )
}


export default Weather
